<template>
    <div class="profile-page">
        <div class="mobile-hide asset-account-container">
            <div class="sidebar">
                <navSidebar :menuItems="menuList" :sidebarTitle="sidebarTitle" :selectedItem="selectedItem"></navSidebar>
            </div>
            <div class="main-content">
                <div class="heading">{{$t('main.AccountPage.Account')}}</div>
                <div class="body">
                    <div class="card">
                        <div class="profile-img"><img src="../../assets/eayon.jpg" alt=""></div>
                        <div class="name">EayonHe</div>
                        <div class="email">Eay****gle@gmail.com</div>
                    </div>
                    <div class="card">
                        <div class="asset-label">
                            {{$t('main.assetsPage.AssetsValue')}} (USD) <i class="el-icon-view" @click="toggleVisible"></i>
                        </div>
                        <div class="asset-amount">
                            1000.00
                            <span v-if="isViewBalance">{{userInfo.coinAnalog / 100 || 0}}</span>
                            <span v-else>{{balanceStar}}</span>
                        </div>
                        <div class="asset-actions">
                            <button @click="routerPath('/assets/deposit')">{{$t('main.navSidebar.Deposit')}} <i class="el-icon-download"></i></button>
                            <button @click="routerPath('/assets/withdraw')">{{$t('main.navSidebar.Withdraw')}} <i class="el-icon-upload2"></i></button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="mobile-show">
            <div class="login-profile">
                <div class="account-logo">
                    <img v-if="this.isLoggedIn" src="" alt="">
                    <img v-if="!this.isLoggedIn" src="../../assets/unknown-profile.png" alt="">
                </div>
                <div v-if="this.isLoggedIn">
                    Client info here
                </div>
                <div v-if="!this.isLoggedIn">
                    {{$t('main.AccountPage.LoginOrSignup')}}
                </div> 
                <div class="flex-spacer"></div>
                <div v-if="!this.isLoggedIn">
                    <img class="forward-icon" src="../../assets/forward-icon.svg" alt="">
                </div>
            </div>
            <div class="options">
                <div class="icon">
                    <i class="el-icon-lock"></i>
                </div> {{$t('main.AccountPage.ChangePassword')}}
                <div class="flex-spacer"></div>
                <img class="forward-icon" src="../../assets/forward-icon.svg" alt="">
            </div>
            <div class="options">
                <div class="icon">
                    <i class="el-icon-mobile"></i>
                </div> {{$t('main.AccountPage.FA')}}
                <div class="flex-spacer"></div>
                <img class="forward-icon" src="../../assets/forward-icon.svg" alt="">
            </div>
            <div class="options">
                <div class="icon">
                    <i class="el-icon-question"></i>
                </div> {{$t('main.AccountPage.ContactSupport')}}
                <div class="flex-spacer"></div>
                <img class="forward-icon" src="../../assets/forward-icon.svg" alt="">
            </div>
            <div class="options" @click="changeLangEvent()">
                <div class="icon">
                    <div class="National_China" v-if="this.$i18n.locale === 'zh'"></div>
                    <div class="National_USA" v-else></div>
                </div>{{this.$i18n.locale === 'zh' ? $t('main.AccountPage.SwitchToEnglish') : $t('main.AccountPage.SwitchToChinese')}}
                <div class="flex-spacer"></div>
                <img class="forward-icon" src="../../assets/forward-icon.svg" alt="">
            </div>
            <div class="options" v-if="isLoggedIn">
                <div class="icon">
                    <i class="el-icon-right"></i>
                </div> {{$t('main.AccountPage.LogOut')}}
                <div class="flex-spacer"></div>
                <img class="forward-icon" src="../../assets/forward-icon.svg" alt="">
            </div>
            <div class="options" v-if="!isLoggedIn">
                <div class="icon">
                    <i class="el-icon-right"></i>
                </div>{{$t('main.AccountPage.LoginOrSignup')}}
                <div class="flex-spacer"></div>
                <img class="forward-icon" src="../../assets/forward-icon.svg" alt="">
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import navSidebar from "@/components/navSidebar";
import { NavigationItems } from '@/common/navigation-items.js'

export default {
    name: "accountPage",
    data() {
        return {
            isLoggedIn: false,
            userInfo: {},
            sidebarTitle: NavigationItems.userSettingsTitle,
            menuList: NavigationItems.userSettings,
            selectedItem: 'main.AccountPage.Account',
            isViewBalance: true
        };
    },
    computed: { },
    created() {
        let token = this.$common.getCookie('token');
        if(token) {
            this.getUserAccount()
        }
    },
    mounted() {

    },
    components: {
        navSidebar
    },
    methods: {
        getUserAccount (){
            this.$http.userDetails().then((res) => {
                this.isLoggedIn = true;
                if(res && res.data){
                    this.userInfo = res.data.data;
                }
            });
        },
        changeLangEvent() {
            if (this.$i18n.locale === "zh") {
                this.$i18n.locale = "en";
            } else {
                this.$i18n.locale = "zh";
            }
        },
        toggleVisible (){
            this.isViewBalance = !this.isViewBalance
        },
        routerPath(route) {
            this.$router.push(route);
        }
    },
    beforeDestroy (){
    }
};
</script>

<style lang="scss">
    .profile-page {
        .body {
            font-size: 12px;
            display: flex;
            .card {
                width: 170px;
                line-height: 18px;
                height: 90px;
                margin-right: 17px;

                padding: 15px;
                text-align: center;
                border: solid 1px #787B89;
                color: #787B89;

                .name {
                    color: #DDDEE5
                }

                .email {
                    font-size: 11px;
                }

                .profile-img {
                    display: inline-block;
                    border-radius: 50%;
                    height: 50px;
                    width: 50px;
                    overflow: hidden;
                    img {
                        height: 50px;
                    }
                }

                .asset-label {
                    font-size: 12px;
                    color: #787B89;
                    margin: 5px 0;
                    i {
                        color: #DDDEE5;
                        position: relative;
                        margin-left: 2px;
                        &:after{
                            content:"";
                            position:absolute;
                            border-bottom:1px solid #DDDEE5;
                            width:14px;
                            left: 11px;
                            transform: rotate(130deg);
                            transform-origin: 0% 0%;
                        }
                    }
                    
                }

                .asset-amount {
                    font-size: 18px;
                    margin-bottom: 14px;
                    color: #DDDEE5;
                }

                .asset-actions {
                    button {
                        font-size: 11px;
                        color: #787B89;
                        border: 1px solid #787B89;
                        background-color: transparent;
                        max-width: 120px;
                        width: 45%;
                        padding: 5px;
                        cursor: pointer;
                    }
                    :first-child {
                        border-right: none;
                    }
                }
            }
        }

        @media screen and (max-width: 768px) {
            font-size: 13px;
            color: #DDDEE5;
            .forward-icon {
                height: 25px;
                margin-top: 10px;
            }
            .login-profile {
                cursor: pointer;
                display: flex;
                margin-right: 15px;
                padding: 8px 0 8px 8px;
                line-height: 45px;
                height: 45px;
                border-bottom: 7px solid #191c25;
                
                .account-logo {
                    background-color: #202432;
                    margin-right: 15px;
                    img {
                        border-radius: 50%;
                        height: 100%;
                        background-color: red;
                    }
                }
            }
            .options {
                cursor: pointer;
                border-bottom: 1px solid #787b89;
                margin: auto;
                height: 45px;
                width: calc(100% - 30px);
                line-height: 45px;
                display: flex;
                .icon {
                    width: 30px;
                    text-align: center;
                    color: #787b89;
                    font-size: 25px;
                    margin-right: 5px;

                    .National_China, .National_USA {
                        height: 20px !important;
                        width: 30px;
                        margin-top: 12px;
                    }
                    
                    .National_China {
                        background: url("../../assets/China.jpg") no-repeat;
                        background-size: cover;
                    }
                    .National_USA {
                        background: url("../../assets/USA.jpg") no-repeat;
                        background-size: cover;
                    }
                }

            }
        }
    }
</style>